export default {
  name: "Input",
  props: {
    label: {
      type: String,
      required: true,
    },
    value: {
      type: String,
      required: false,
      default: "",
    },
    placeholder: {
      type: String,
      required: false,
      default: "",
    },
    border: {
      type: String,
      required: false,
      default: "rounded-lg border",
    },
    customClass: {
      type: String,
      required: false,
    },
    type: {
      type: String,
      required: false,
      default: "password",
    },
  },
  methods: {
    updateValue: function (value) {
      this.$emit('input', value)
    },
    changeType() {
      this.type = (this.type=='password') ? this.type = 'text' : this.type = 'password';
    }
  }
};
